.CrossControl {
  position: absolute;
  width: 130px;
  height: 130px; }
  .CrossControl .arrow {
    position: absolute;
    background: black;
    width: 33%;
    height: 33%;
    border-radius: 20%;
    -webkit-box-shadow: 1px 1px 6px inset white;
            box-shadow: 1px 1px 6px inset white; }
    .CrossControl .arrow:active {
      background: grey; }
    .CrossControl .arrow.up, .CrossControl .arrow.down {
      left: 50%;
      margin-left: -16.5%; }
    .CrossControl .arrow.down {
      bottom: 0px; }
    .CrossControl .arrow.left, .CrossControl .arrow.right {
      top: 50%;
      margin-top: -16.5%; }
    .CrossControl .arrow.right {
      right: 0px; }



